'use client'

import Link from 'next/link'
import { Sparkles } from 'lucide-react'

interface LogoProps {
  className?: string
  showIcon?: boolean
}

export function Logo({ className = '', showIcon = true }: LogoProps) {
  return (
    <Link
      href="/"
      className={`inline-flex items-center gap-2 group ${className}`}
    >
      {showIcon && (
        <div className="relative">
          <div className="absolute inset-0 bg-primary/20 rounded-full blur-md group-hover:bg-primary/30 transition-colors" />
          <div className="relative w-10 h-10 rounded-full bg-gradient-to-br from-primary to-primary/70 flex items-center justify-center">
            <Sparkles className="w-5 h-5 text-white" />
          </div>
        </div>
      )}
      <span className="font-dancing text-3xl md:text-4xl font-bold text-foreground group-hover:text-primary smooth-transition tracking-tight">
        Fashino
      </span>
    </Link>
  )
}

